<template>
  <div class="m-instagram-embed">
    <blockquote class="instagram-media" :data-instgrm-permalink="fullInstagramLink" data-instgrm-version="8" style=" background:#FFF; border:0; border-radius:3px; box-shadow:0 0 1px 0 rgba(0,0,0,0.5),0 1px 10px 0 rgba(0,0,0,0.15); margin: 1px; max-width:658px; padding:0; width:99.375%; width:-webkit-calc(100% - 2px); width:calc(100% - 2px);">
      <div style="padding:8px;"><div style=" background:#F8F8F8; line-height:0; margin-top:40px; padding:50% 0; text-align:center; width:100%;"> <div style=" background:url(data:image/png;base64,); display:block; height:44px; margin:0 auto -44px; position:relative; top:-22px; width:44px;"></div></div><p style=" color:#c9c8cd; font-family:Arial,sans-serif; font-size:14px; line-height:17px; margin-bottom:0; margin-top:8px; overflow:hidden; padding:8px 0 7px; text-align:center; text-overflow:ellipsis; white-space:nowrap;"><a :href="fullInstagramLink" style=" color:#c9c8cd; font-family:Arial,sans-serif; font-size:14px; font-style:normal; font-weight:normal; line-height:17px; text-decoration:none;" target="_blank">A post shared by {{ username }</a> on <time style=" font-family:Arial,sans-serif; font-size:14px; line-height:17px;">{{ time }</time></p></div>
    </blockquote>
  </div>
</template>

<script>

export default {
  name: 'instagram-embed',
  props: [
    'instaId',
    'username',
    'time'
  ],
  created () {
    let instagramEmbedScript = document.createElement('script')
    instagramEmbedScript.setAttribute('src', 'https://www.instagram.com/embed.js')
    document.head.appendChild(instagramEmbedScript)
  },
  computed: {
    /**
     * The URL of the instagram embed.
     */
    fullInstagramLink () {
      return `https://www.instagram.com/p/${this.instaId}/`
    }
  }
}
</script>

<style lang="scss">
.m-instagram-embed {
  padding: .5em;
}
</style>



